import React from "react";
import styled, { ThemeProvider } from "styled-components";

import langs from "langs";

import Nav from "components/Nav";

const El = styled.div`
  height: 100vh;

  ${({ theme }) => `
    background: linear-gradient(${theme.background[0]}, ${theme.background[1]});
  `};
`;

const Text = styled.h1`
  display: none;
  font: 135px/125px "Delius Swash Caps", Georgia, serif;
  margin: 0;
  padding: 0;
  line-height: 125px;
  letter-spacing: -3px;
  cursor: default;
`;

const App = ({ lang }) => (
  <ThemeProvider theme={langs[lang].colors}>
    <El>
      <Text>{langs[lang].text}</Text>
      <pre>{JSON.stringify(langs[lang], null, 2)}</pre>
      <Nav lang={lang} />
    </El>
  </ThemeProvider>
);

App.defaultProps = {
  lang: "english",
};

export default App;



